import React from 'react';
import {Paper, Typography, Modal} from "@mui/material";
import {Link, NavLink} from "react-router-dom";

interface MobileNavProps {
isOpen: boolean,
handleClose: () => void
}

interface TextUnitProps {
    text: string,
    navLink: string
}

const TextUnit = ({text, navLink}: TextUnitProps) => {

    return (
        <Link to={navLink} style={{
            textDecoration: 'none'}}>
                    <Typography variant="h5" style={{
                    fontFamily: 'Main',
                    fontWeight: 'bold',
                    padding: 10,
                    cursor: 'pointer',
                    color: 'black',
                    borderRadius: 5,
                    textAlign: 'center'
                }}
                            sx={{
                                "&:hover": {boxShadow: "rgba(0, 0, 0, 0.4) 0px 5px 15px"},
                                transition: "all 0.3s ease-in-out",
                            }}
                           >
                            {text}
                            </Typography> 
            </Link>

    )}


export const MobileNav = ({isOpen, handleClose}: MobileNavProps) => {
    return (
        <Modal
            open={isOpen}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Paper style={{display: 'flex', flexDirection: 'column', margin: 50, marginTop: '3.5rem', outline: 'none', padding: 10}}>
            <TextUnit text="DOJO" navLink="/dojo" />
        <TextUnit text="LORE" navLink="/lore" />
        <TextUnit text="AMAI MAP" navLink="/roadmap" />
        <TextUnit text="GALLERY" navLink="/gallery" />
        <TextUnit text="STORE" navLink="/store" />
            </Paper>
        </Modal>
    );
};

